body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* FLEX */
.flex {
  display: flex; }
.vertical {
  flex-direction: column; }
.horizontal, .horizontal-only {
  flex-direction: row; }
@media screen and (max-width: 720px){
  .horizontal {
    flex-direction: column; }
  .horizontal-mb {
    flex-direction: row;
  }
}

.flex-1 { flex: 1; }
.flex-2 { flex: 2; }
.flex-3 { flex: 3; }
.flex-4 { flex: 4; }
.flex-5 { flex: 5; }

.flex-center {
  justify-content: center;
  align-items: center; }
.flex-start {
  justify-content: center;
  align-items: flex-start;
}

/* TEXT CENTER */
.text-center {
  text-align: center; }

.container, .container-small {
  width: calc(100%-20px);
  padding: 0 10px;
  max-width: 1280px;
  margin: 0 auto; }
.container-small {
  max-width: 1040px; }

/* Topbar */
.Topbar {
  /* background-color: transparent; */
  background-color: #3d3d51;
  padding: 8px 0; }

.Topbar span {
  font-size: 0.75rem;
  /* color: #000; */
  color: #FFF;
  cursor: pointer;
}
.Topbar .topbar-item {
  border-right: 1px solid #fff; }
.Topbar .topbar-item:last-child {
  border-right: 0; }

@media screen and (max-width: 720px){
  .Topbar {
    padding: 1px;
  }
  .Topbar .topbar-item {
    padding: 5px;
    text-align: center; }
}

.Header {
  padding: 30px 0;
}
.logo {
  cursor: pointer;
  width: 95%;
}
.input-pesquisa {
  font-size: 1rem;
  padding: 10px 25px;
  width: 70%;
}

@media screen and (max-width: 720px){
  .logo {
    padding: 0 0 15px 0;
    width: 70%;
  }
}

.btn {
  padding: 10px 20px;
  cursor: pointer;
}
.btn-pesquisa, .btn-cart {
  color: #f4f4f4;
  background-color: #28274A;
  border: 0;
}
.btn-pesquisa:hover, .btn-cart:hover {
  background-color: rgb(22, 21, 44);
}
.btn-cart span {
  color: #28274A;
  font-weight: bold;
  font-size: 1.1rem;
  padding: 0 5px;
  margin: 0 10px;
  background-color: #f4f4f4;
  border-radius: 50px;
}

@media screen and (max-width: 720px){
  .btn-cart {
    margin: 10px 0 -10px 0;
  }
}

.Categorias {
  background-color: #3F3F3F;
}
.categoria-item {
  font-weight: bold;
  color: #FFF;
  padding: 12px 0;
  font-size: 0.9rem;
  border-right: 0.5px solid rgba(255, 255, 255, 0.865);
  cursor: pointer; }
.categoria-item:last-child {
  border-right: 0px; }
.categoria-item:hover {
  background-color: rgba(255, 255, 255, 0.2); }
.color-red {
  color: rgb(211, 7, 7); }

@media screen and (max-width: 720px){
  .Categorias {
    overflow-x: scroll;
  }
  .categoria-item {
    padding: 5px;
    text-align: center;
  }
}

.breadcrumbs {
  color: #999;
  font-size: 0.8rem;
  margin: 20px;

}
.img-thumb {
  border: 1px solid #cccc;
  margin: 2px;
  cursor: pointer;
}
.img-main {
  margin: 5px;
}
.titulo-produto h2 {
  margin: 0;
  color: #474747; }
.titulo-produto span {
  color: #999;
  font-size: 0.8rem; }
.estrelas {
  margin: 5px 0;
}

.price {
  margin: 20px 0;
}
.price h2 {
  margin: 0;
  font-size: 2rem;
}
.price span {
  color: #777;
  font-size: 0.9rem; }

  
.options .label span {
  font-size: 0.85rem;
  color: #555;
}

.opcoes-roupa {
  margin: 10px 0;
}
.opcoes-roupa .opcoes-item {
  width: 80px;
  cursor: pointer;
  margin: 5px 0;
  border: 1px solid #474747; }
.opcoes-roupa .opcoes-item-active, .opcoes-roupa .opcoes-item:hover {
  border: 3px solid #474747;
}

.opcoes-tam {
  display: flex;
  flex-direction: row;
}
.opcoes-tam .opcoes-item {
  padding: 15px;
  width: 30px;
  background-color: #f0f0f0;
  /* border: 0.5px solid #aaaa; */
  margin: 10px 5px;
  cursor: pointer;
  font-weight: bold;
}
.opcoes-tam .opcoes-item:first-child {
  margin: 10px 5px 10px 0;
}
.opcoes-tam .opcoes-item:hover, .opcoes-tam .opcoes-item-active {
  background-color:  #ffffff;
}

.quantidade {
  margin: 10px 0;
}
.quantidade .label {
  font-size: 0.85rem;
  color: #555;
}
.qtd-input {
  padding: 10px 15px;
  color: #222;
  font-size: 1rem;
  margin: 5px 0;
  border: 1px solid #aaa;
  width: 50px;
}

.btn.btn-buy {
  background-color: #28274A;
  color: #FFF;
  font-size: 1.5rem;  
  font-weight: bold;
  padding: 15px 25px;
  margin-top: 20px;
  border: 0;
}
.btn.btn-buy i {
  margin: 0 10px;
}
.btn.btn-buy:hover {
  background-color: rgb(27, 26, 56);
}
.whishlist {
  margin: 10px 0;
  color: #777;
  cursor: pointer;
}
.whishlist i {
  margin-right: 10px;
}
.whishlist:hover {
  color: #272727;
}

@media screen and (max-width: 720px){
  .btn.btn-buy {
    width: 100%;
  }
}

.Descricao {
  margin: 0 0 15px 0;
  padding: 0 20px 20px 20px;
  background-color: #FFF;
}
.Descricao .titulo {
  font-size: 1.5rem;
  padding: 10px 0;
  color: #474747;
}
.Descricao span {
  padding: 5px 0;
}

.table {
  background-color: #FFF;
}
.table th {
  background-color: #28274A;
  padding: 10px 0;
  color: #FFF;
}
.table th:first-child {
  background-color: transparent;
}
.table tr td {
  max-width: 50px;
  padding: 10px 0;
}
.table tr td:first-child {
  max-width: 100px;
}

@media screen and (max-width: 720px){
  .table th {
    padding: 5px;
  }
}

.Newsletter{
  margin: 10px 0;
  padding: 15px 0;
  background-color: #222;
  color: #FFF;
}
.email-input {
  font-size: 1rem;
  padding: 10px 15px;
  border: 0.5px solid #AAA;
  border-right: 0;
}
.btn-cta-newsletter {
  font-size: 1rem;
  padding: 10px 15px;
  border: 0.5px solid #AAA;
  border-left: 0;
  font-weight: bold;
  background-color: #28274A;
  color: #ffefff;
}
.btn-cta-newsletter:hover {
  background-color: rgb(21, 20, 46); }

@media screen and (max-width: 720px){
  .Newsletter small {
    text-align: center;
  }
  .newsletter-email {
    padding: 10px 0;
  }
}


.Relacionados {
  margin: 30px 0; }
.pic-related-container {
  cursor: pointer;
  margin: 10px;
  padding: 10px;
  border-radius: 3px; }
.pic-related-container:hover {
  box-shadow: 0 0 5px #ccc; }
.pic-related-container .titulo {
  height: 120px; }
.pic-related-container .preco {
  font-size: 1.5rem;
  font-weight: bold; }

.Institucional {
  background-color: rgb(48, 48, 48);
  padding-bottom: 30px;
}
.Institucional h3 {
  color: #fff;
  margin: 30px 0 10px 0;
}
.Institucional hr {
  width: 80%;
}
.institucional-item {
  font-size: 0.9rem;
  padding: 2px 0;
  padding-left: 20px;
  color: rgb(228, 228, 228);
  cursor: pointer;
}
.institucional-item:hover {
  color: rgb(171, 171, 171);
}
  
.Footer {
  padding: 20px 0;
  font-size: 0.75rem;
  background-color: rgb(23, 23, 23);
  color: #FFF; }
.Footer span {
  margin: 1px 0; }

